import {
  OnInit, Directive, Input, ElementRef, AfterViewInit, Renderer2, OnDestroy, TemplateRef,
  ComponentFactory, ViewContainerRef, ComponentFactoryResolver, NgZone, ComponentRef
} from '@angular/core';
import { TooltipComponent } from './tooltip-component/tooltip.component';
@Directive({
  selector: '[farrisTooltip]'
})
export class FarrisTooltipDirective implements OnInit, AfterViewInit, OnDestroy {
  private tooltipEnable: boolean = true;
  private _componentRef: ComponentRef<TooltipComponent>;
  // 5æ7æ¥è¿½å è¿ç§åæ³ï¼æ§å¶ç¦ç¨ãå¯ç¨
  // åæ¥çæ¶åvalueæ¯ç©ºå­ç¬¦ä¸²
  @Input('farrisTooltip')
  set enableTooltip(value: any) {
    if (typeof value=='string'&&value == '') {
      this.enableOrDisable(true);
    } else {
      this.enableOrDisable(value);
    }
  }
  get enableTooltip() {
    return this.tooltipEnable;
  }
  /* æç¤ºææ¬ */
  @Input() text = '';
  // æç¤ºæ¡æ ·å¼
  @Input() tipCls='';
  // æç¤ºæ¡å®½åº¦
  @Input() tipWidth='';
  /* ä½ç½® */
  @Input() placement = 'top';

  private triggerMode = 'hover';
  /* è§¦åæ¹å¼ */
  @Input()
  set trigger(value) {
    if (value) {
      if (value !== this.triggerMode) {
        this.triggerMode = value;
        this.unbindEvent();
        this.bindEvent();
      }
    }
  }
  get trigger() {
    return this.triggerMode;
  }

  /* ç¦ç¨---è·å¶ä»æ§ä»¶çå±æ§å®¹æå²çªï¼éæ¸åºå¼ã
  * æ¯å¦æé®å¯ä»¥è®¾ç½®ç¦ç¨ä¸è§¦åç¹å»ï¼ä½æ³ç»åºç¹å»æç¤ºï¼è¿æ ·å°±å²çªäº
  */
  @Input()
  set disable(value) {
    this.enableOrDisable(!value);
  }
  get disable() {
    return !this.tooltipEnable;
  }

  @Input() showTip: boolean = true;


  /* åå®¹æ¨¡æ¿ */
  @Input() content: string | TemplateRef<any>;

  mouseenterEvent: any;
  mouseleaveEvent: any;
  clickEvent: any;
  clickOutEvent: any;
  focusDownEvent: any;
  focusUpEvent: any;
  docBody: any;
  // æ¯å¦ç»å®äºäºä»¶
  private hasBindEvent = false;

  /* çææç¤ºç»ä»¶ */
  componentFactory: ComponentFactory<TooltipComponent> = this.resolver.resolveComponentFactory(TooltipComponent);

  tooltipRef: any;
  tooltip: TooltipComponent;
  constructor(
    private el: ElementRef,
    private render: Renderer2,
    private viewContainerRef: ViewContainerRef,
    private resolver: ComponentFactoryResolver
  ) { }

  ngOnInit() {
    this.bindEvent();
  }

  ngAfterViewInit(): void {

  }

  ngOnDestroy() {
    this.unbindEvent();
    this.removeTooltip();
  }
  private enableOrDisable(value) {
    this.tooltipEnable = value;
    if (this.tooltipEnable) {
      this.bindEvent();
    } else {
      this.unbindEvent();
    }
  }

  bindEvent() {
    if (!this.tooltipEnable) {
      return;
    }
    // æ¯å¦å·²ç»å®äºä»¶
    if (this.hasBindEvent) {
      return;
    }
    if (this.trigger === 'click') {
      // é¼ æ ç¹å»
      this.clickEvent = this.render.listen(this.el.nativeElement, 'click', (e: MouseEvent) => {
        e.stopPropagation();
        this.appendTooltip();
        this.clickOutEvent = this.render.listen(document, 'click', () => {
          this.removeTooltip();
          this.clickOutEvent();
        });
      });
    } else if (this.trigger === 'focus') {
      // èç¦
      // @Todo éè¦æinputç­ç»ä»¶åæ®éç»ä»¶åå¼
      this.focusDownEvent = this.render.listen(this.el.nativeElement, 'mousedown', (e: MouseEvent) => {
        e.preventDefault();
        this.appendTooltip();
      });
      this.focusUpEvent = this.render.listen(this.el.nativeElement, 'mouseup', (e: MouseEvent) => {
        e.stopPropagation();
        this.removeTooltip();
      });
    } else {
      // é¼ æ ç§»å¨ä¸å»
      this.mouseenterEvent = this.render.listen(this.el.nativeElement, 'mouseenter', (e: MouseEvent) => {
        e.stopPropagation();
        this.appendTooltip();
      });
      this.mouseleaveEvent = this.render.listen(this.el.nativeElement, 'mouseleave', (e: MouseEvent) => {
        e.stopPropagation();
        this.removeTooltip();
      });
    }
    this.hasBindEvent = true;
  }

  /* è§£ç»äºä»¶ */
  unbindEvent() {
    // é¼ æ ç§»ä¸å»
    if (this.mouseenterEvent) {
      this.mouseenterEvent();
    }
    if (this.mouseleaveEvent) {
      this.mouseleaveEvent();
    }
    if (this.clickEvent) {
      this.clickEvent();
    }
    if (this.clickOutEvent) {
      this.clickOutEvent();
    }
    if (this.focusDownEvent) {
      this.focusDownEvent();
    }
    if (this.focusUpEvent) {
      this.focusUpEvent();
    }
    this.hasBindEvent = false;
  }

  /* body  ç§»é¤tooltip */
  removeTooltip() {
    if (this.tooltip) {
      this.render.removeChild(document.body, this.tooltip.el.nativeElement);
      this._componentRef.changeDetectorRef.markForCheck();
      this._componentRef.changeDetectorRef.detectChanges();      
      this._componentRef.destroy();
      this.viewContainerRef.clear();
      this.tooltip = null;
      this._componentRef=null;
    }
  }

  private appendTooltip() {
    if (!this.showTip) return;
    this.generateTooltip();
    this.updateViewProps(this.placement, this.content, this.el.nativeElement.getBoundingClientRect());
  }

  /* æ´æ°tooltipç»ä»¶å±æ§ */
  private updateViewProps(placement: string, content: string | TemplateRef<any>, hostBoundingClientRect: object) {
    this.tooltip.placement = placement;
    this.tooltip.content = content;
    this.tooltip.hostBoundingClientRect = hostBoundingClientRect;
    this.tooltip.tipCls=this.tipCls;
    this.tooltip.tipWidth=this.tipWidth;
    this._componentRef.changeDetectorRef.markForCheck();
    this._componentRef.changeDetectorRef.detectChanges();
  }

  /* æé tooltipç»æ */
  private generateTooltip() {    
    this._componentRef=this.viewContainerRef.createComponent(this.componentFactory)
    this.tooltip =  this._componentRef.instance;
    // ç¶åç´ ä¸­ç§»é¤  æ·»å å°bodyä¸­
    this.render.removeChild(this.render.parentNode(this.el.nativeElement), this.tooltip.el.nativeElement);
    this.render.appendChild(document.body, this.tooltip.el.nativeElement);
  }
}
