import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FarrisTooltipDirective } from './tooltip.directive';
import { FInputTipsDirective } from './inputTips.directive';
import { TooltipComponent } from './tooltip-component/tooltip.component';
import { AttentionService } from './attention.service';
import { tooltipHtmlPipe } from './tooltip-safe-html.pipe';
@NgModule({
  declarations: [FarrisTooltipDirective, TooltipComponent, FInputTipsDirective,tooltipHtmlPipe],
  imports: [CommonModule],
  providers: [AttentionService],
  entryComponents: [TooltipComponent],
  exports: [FarrisTooltipDirective, TooltipComponent, FInputTipsDirective]
})
export class FarrisTooltipModule { }
